export default "/*\n * Booking\n */\n\n.cover-container {\n    background-image: url('footer.jpg');\n    background-color: rgba(0, 0, 0, .7);\n    background-blend-mode: overlay;\n    background-size: cover;\n    background-position: center;\n    background-repeat: no-repeat;\n    height: 23rem;\n    padding-top: 10rem;\n    padding: 10rem 48px 3rem;\n}\n\n.cover-container h1 {\n    font-size: 50px;\n    font-weight: bold;\n    line-height: normal;\n    color: #fff;\n    text-transform: none;\n}\n\n/*\n * Contact\n */\n\n.contact-section {\n    background-color: #f3f3f3;\n    float: left;\n    width: 100%;\n    padding: 1.5rem 0;\n}\n\n.contact-section .section-heading {\n    padding: 48px;\n    float: left;\n    width: 100%;\n}\n\n.contact-section .section-heading h3 {\n    letter-spacing: 0.5em;\n    color: #8C8C8C;\n    font-size: 1.25rem;\n    margin: 0;\n    font-weight: 800;\n}\n\n.contact-section .section-heading .form-control {\n    border-radius: 0;\n    background-color: #f3f3f3;\n    color: #000;\n    border: 0;\n    border-bottom: 4px solid #bada55;\n    padding: 3px;\n    margin: 0;\n    text-overflow: ellipsis;\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    box-shadow: none;\n    margin-bottom: 1.25rem;\n    font-size: 1.5rem;\n    font-weight: 800;\n    resize: none;\n}\n\n.contact-section .section-heading .btn-green {\n    background-color: #9bcb6c;\n    border: 0;\n    cursor: pointer;\n    color: #000;\n    font-size: 1.5rem;\n    border-radius: 0;\n    font-weight: 800;\n    padding: 6px 20px;\n    height: 50px;\n    line-height: 40px;\n    width: 240px;\n    box-shadow: none;\n    margin-top: 1rem;\n}\n\n.section-testimonial {\n    float: left;\n    width: 100%;\n    padding: 0 48px;\n}\n\n.section-testimonial .section-heading {\n    border-top: 4px solid #ccc;\n    padding: 4rem 0;\n}\n\n.section-testimonial .section-heading h2 {\n    letter-spacing: 0.5em;\n    color: #8C8C8C;\n    font-size: 1.25rem;\n    margin: 0;\n    font-weight: 800;\n    margin-bottom: 2rem;\n}\n\n/* Bubble Slider */\n\n.bubble blockquote {\n    margin: 10px 10px 0;\n    background: #fff;\n    padding: 60px;\n    position: relative;\n    border: none;\n    border-radius: 8px;\n    font-size: 1.25em;\n    color: #000;\n}\n\n.bubble blockquote:before,\n.bubble blockquote:after {\n    content: \"\\201C\";\n    position: absolute;\n    font-size: 80px;\n    padding: 10px;\n    line-height: 1;\n    color: #000;\n}\n\n.bubble blockquote:before {\n    top: 0;\n    left: 10px;\n}\n\n.bubble blockquote:after {\n    content: \"\\201D\";\n    right: 10px;\n    bottom: -.5em;\n}\n\n.bubble div {\n    width: 0;\n    height: 0;\n    border-left: 0 solid transparent;\n    border-right: 20px solid transparent;\n    border-top: 20px solid #fff;\n    margin: 0 0 0 60px;\n    margin-bottom: 10px;\n}\n\n.bubble cite {\n    padding-left: 20px;\n    font-size: 1.25em;\n    color: #000;\n}\n\n@media screen and (max-width: 767px) {\n    .cover-container {\n        height: auto;\n        padding: 15rem 18px 3rem;\n    }\n    .cover-container h1 {\n        font-size: 33px;\n    }\n    .section-testimonial {\n        padding: 0 1.5rem;\n    }\n    .contact-section .section-heading {\n        padding: 4rem 1.5rem;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"